import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push } from 'react-router-redux';

import './LandingPage.css';
import agent from '../../agent';
import NavBar from './components/NavBar';

import { store } from '../../store';

import { APP_NAME } from '../../constants/otherConstants';
import {WEB_TOKEN} from '../../constants/otherConstants'
class LandingPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loggedIn:false
    };
  }

  componentWillMount() {
    let token = window.localStorage.getItem(WEB_TOKEN)
    if(token){
      this.setState({loggedIn:true})
    }else{
      this.setState({loggedIn:false})
    }
  }


  componentWillReceiveProps(np) {

  }

  render() {
    return (
        <div className='gradient-bg-landing-page' >
      
        <div className="bg-img">
        <div className="main-background">
          <NavBar loggedIn={this.state.loggedIn} />
          <div className="main-div-width ">
            <div className="safe-home-div">
              <div className="tc center white home-page-header">
                <h1>One Link For Everything</h1>
                <p>
                  {' Get all of your links under one screen'}
                </p>
                {
                  this.state.loggedIn ?  
                  <button type="button" onClick={() => store.dispatch(push('/account'))}>
                  
                  <p>Go To My Account</p>
                </button>:
                <button type="button" onClick={() => store.dispatch(push('/login'))}>
                  
                <p>Start your journey</p>
              </button>
                }
                
              </div>

            </div>

          </div>
        </div>
      </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => ({

});

const mapDispatchToProps = (dispatch) => ({

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(LandingPage);
