/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
// import { push } from 'react-router-redux';

import './HomePage.css';

import agent from '../../agent';
import ProfilePIc from '../../common/Components/ProfilePIc';
import Loader from '../../common/Components/Loader';
import { store } from '../../store';
import { GET_USER_DETAILS } from '../../constants/actionTypes';
import Logo from '../../common/Components/Logo';
import { APP_NAME } from '../../constants/otherConstants';

class HomePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      img: null,
      loading: false,
      user: { profile_pic: null, link: [], username: '' },
      error: '',
    };
    this.links = [];
  }

  componentWillMount() {
    this.setState({ loading: true });
    if (this.props.parent) {
      if (this.props.parent === 'admin-page') {
        if (this.props.links) {
          this.links = this.props.links.links;
        }
        if (this.props.user) {
          this.setState({ user: this.props.user.user });
        }
      }
    } else {
      const { userName } = this.props.match.params;
      const data = { username: userName };

      this.props.getUserDetails(data);
    }
  }


  componentWillReceiveProps(np) {
    if (this.props.parent === 'admin-page') {
      if(np.links && np.links.links !== this.links){
        this.links = np.links.links
        this.setState({loading:false})
      }
    } else if (np.user) {
      if (this.state.user !== np.user.user) {
        if (np.user.user.links) {
          this.links = np.user.user.links;
        }
        this.setState({ user: np.user.user, loading: false });
      }
    }
  }

  render() {
    const { username, profile_pic } = this.state.user;

    return (
      <div className="gradientbg">
        <div className="main-div-width home-page-div ">
          <div className="dyna-width">
            <div className=" center">
              <article className="center  tc ">

                <ProfilePIc photo={profile_pic} changeProfile={false} />
                <h1 className="subheading white center tc" style={{ color: 'white' }}>
                  {`@${username}`}
                </h1>
                <p className="err-txt">{`${this.state.error}`}</p>
              </article>
            </div>
            <div className="tc mt4 dyna-width">
              {
                this.links.map((link, i) => {
                  if (link.visible) {
                    return (
                      <a href={link.url} key={i} className="link dyna-width" target="_blank" rel="noopener noreferrer">
                        {link.icon ? <img src={link.icon} alt="" /> : null}
                        <p className="center ">
                          {`${link.title}`}
                        </p>
                      </a>
                    );
                  }
                  return null;
                })
              }
            </div>
          </div>
          <footer>
            <Logo logostyles={{ display: 'flex', flexDirection: 'row' }} parent="public-home-page" />
          </footer>
        </div>
        {
            this.state.loading
              ? <Loader />
              : null
        }
      </div>
    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  links: state.AdminPageReducers.links,
});

const mapDispatchToProps = (dispatch) => ({
  getUserDetails: (user) => store.dispatch({ type: GET_USER_DETAILS, payload: agent.Auth.getUserDetails(user) }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(HomePage);
